
import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);


var store = new Vuex.Store({
	state:{
		n:8
	},
	getters:{
		isGood(state){
			return state.n>60 ?"及格" :"不及格"
		}
	},
	mutations:{
		changeUp(state,p){
			return state.n+=p
		},
		Up(state,p){
			return state.n+=p
		}
	},
	actions:{
		changeUp(context,p){
			context.commit("changeUp",p)
		}
	}
});


export default store ;



