/*用export把方法暴露出来*/
/*设置cookie*/

export function setCookie(name,value,days){//存cookie days天后过期
    var oDate = new Date();
    oDate.setDate(oDate.getDate() + days);
    document.cookie = name + "=" +  value  + ";expires = " + oDate;
}

