import {getCookie,setCookie} from "../assets/cookie.js"
let router=new Router({
  routes: [
  	{
  		path:"/",
  		redirect:"/home"
  	},
    {
      path: '/home',
      name: 'home',
      component: () => import('@/components/page/home'),
      
      children:[
		      {
			  		path:"/home",
			  		redirect:"/index"
		  	},
      	{
      		path:"/index",
      		name:"index",
      		component: () => import('@/components/page/index'),
			  		children:[
			  		{
			  			path:"/index",
			  			redirect:"/hot"
			  		},
		  			{
		  				path:"/hot",
		  				name:"hot",
		  				component:() => import('@/components/page/hot')
		  			},
		  			{
		  				path:"/replace",
		  				name:"replace",
		  				component:() => import('@/components/page/replace')
		  			}
		  		]
      	},
      	{
      		path:"/index",
      		name:"index",
      		component:() => import('@/components/page/index')
      	},
      	{
      		path:"/cinema",
      		name:"cinema",
      		component:() => import('@/components/page/cinema'),
      		children:[
				  	{
				  		path:"/linkage",
				  		name:"linkage",
				  		component:() => import('@/components/page/linkage')
				  	}
				  	
      		]
      	},
		  	{
		  		path:"/search",
		  		name:"search",
		  		component:() => import('@/components/page/search')
		  	},
		  	{
		  		path:"/mylogin",
		  		name:"mylogin",
		  		meta:"aa",
		  		component:() => import('@/components/page/mylogin')
		  	}
      ]
    },
  	{
  		path:"/mine",
  		name:"mine",
  		meta:"aa",
  		component:() => import('@/components/page/mine')
  	},
  	{
  		path:"/detail",
  		name:"detail",
  		component:() => import('@/components/page/detail')
  	},
  	{
  		path:"/register",
  		name:"register",
  		component:() => import('@/components/page/register')
  	},
  	{
  		path:"/goback",
  		name:"goback",
  		component:() => import('@/components/page/goback')
  	},
  	{
  		path:"/Tdetail/:id",
  		name:"Tdetail",
  		component:() => import('@/components/page/Tdetail')
  	}
  	
  ]
})


